*{
    margin: 0;
    padding: 0;
    font-family: cursive;
    box-sizing: border-box;
}

body{
    background:linear-gradient(90deg, rgb(114, 24, 114), rgb(6, 61, 116), rgb(103, 4, 20));
}

.row{
    width: 50%;    
    text-align: center;
    margin: 100px auto 76px;
    border: 0;
    border-radius: 30px;
    box-shadow: 20px 20px 60px black;
    padding: 40px 0;
}

.row h1{
    font-size: 44px;
    font-weight: 600;
    margin: 20px 0 50px;
    color: rgba(255, 255, 255, 0.677);
    
}

.col{
    padding: 20px;
    width: 100%;
    margin-bottom: 14px;
  
}

.col input{
    width: 400px;
    height: 45px;
    border-radius: 20px;
    font-size: 22px;
    padding: 0 17px;
    color: white;
    background-color: rgba(255, 255, 255, 0.534);
    outline-color: white;
    border: 0;
    margin-right: 20px;

}

::placeholder{
    color: white;

}

.col button{
    height: 45px;
    border-radius: 50%;
    font-size: 22px;
    padding: 0 17px;
    color: rgb(233, 65, 65);
    background-color: rgba(39, 2, 65, 0.89);
    outline-color: white;
    
}

.all_notes{
    width: 300px;
    justify-self: center;
    margin: 10px 0 0 185px;
    align-items: center;
    display: grid;
    grid-template-columns: 1fr;
   
}

.all_notes h4{
    width: 100%;
    background: #ffffff7c;
    height: 40px;
    font-size: 20px;
    margin: 10px auto 30px;
    padding: 3px 13px;
    border-radius: 10px;
    overflow: hidden;
    color: white;
    text-align: start;
    align-self: center;
    overflow:auto;
  
    
}

.row span{
    margin:5px 0 2px 14px;
    border-radius: 4px;
    background: rgb(184, 15, 52);
    float: right;
    align-self: center;
    
}

.row span:hover{
    scale: 1.1;
    cursor: pointer;
}

.clear{
    width: 120px;
    height: 44px;
    border-radius: 17px;
    font-size: 24px;
    color: white;
    background: linear-gradient(45deg, rgba(98, 27, 145, .4), rgba(255, 0, 0, 0.429), rgba(98, 27, 145, 0.505));
    display: none;
}

.shak_Animation{
    animation: shake .1s linear 10;
}

@keyframes shake{
    0%{
        transform: translateX(0);
    }

    25%{
        transform: translateX(-2px);
    }

    50%{
        transform: translateX(0);
    }

    75%{
        transform: translateX(2px);
    }

    100%{
        transform: translateX(0);
    }
}